import React, { memo, Component } from "react";
import { Helmet } from "react-helmet";
import "./OnSiteUpload.css";
import QRCode from "qrcode.react";
import { ONSITE_URL } from "../../../actions/api";

const OnsiteStripStyle = ({
onsite_txt_font_variation,
onsite_txt_css_font,
onsite_txt_font_size,
}) => {
return (
    <Helmet>
    <link
        href={`https://fonts.googleapis.com/css?family=${onsite_txt_css_font}:${onsite_txt_font_variation}`}
        rel="stylesheet"
    />
    <link
        href={`https://fonts.googleapis.com/css?family=${onsite_txt_font_size}:${onsite_txt_font_variation}`}
        rel="stylesheet"
    />
    </Helmet>
);
};

class OnSiteBanner extends Component {
render() {
    const { wall } = this.props;
    const { UgcSettings, UserRule } = wall;

    return (
    <>
        {UgcSettings && Object.keys(UgcSettings).length > 0 ? (
        <OnsiteStripStyle {...UgcSettings} />
        ) : null}
        <div className={`display-onsiteupload ${(UgcSettings.onsite_qr_code_btn_text.length && String(UgcSettings.onsite_qr_code_btn_text).length > 30) ? 'display-onsiteupload-text' : ''} ${(UgcSettings.onsite_overlay_poisition == "1" && UserRule.branding)? 'qr-with-logo' : ''} onsiteupload_${
            UgcSettings.onsite_overlay_poisition == "1"
            ? `bottom_right`
            : UgcSettings.onsite_overlay_poisition == "2"
            ? `bottom_left`
            : UgcSettings.onsite_overlay_poisition == "3"
            ? `top_right`
            : UgcSettings.onsite_overlay_poisition == "4"
            ? `top_left` : 
            `bottom_right`
        }_position`}
        style={{
            height: `${
            UgcSettings.onsite_overlay_height
                ? UgcSettings.onsite_overlay_height
                : 120
            }px`,
        }}
        >
        <div className="overlay_bg" style={{
            backgroundColor: UgcSettings.onsite_btn_color
            ? UgcSettings.onsite_btn_color
            : UgcSettings.onsite_bg_color
        }}></div>
        <div className="positon-relative" style={{
            color: UgcSettings.onsite_txt_color
        }}>
            <div className="onsite_barcode_" style={((UgcSettings.onsite_qr_code_btn_text.length && String(UgcSettings.onsite_qr_code_btn_text).length > 30) && (UgcSettings.onsite_overlay_poisition == "1" || UgcSettings.onsite_overlay_poisition == "3")) ? { order: 2 } : {}}>
                <QRCode
                    style={{ height: "100%", width: "100%" }}
                    value={`${ONSITE_URL}${wall.Wall.id}${`?social-wall=true&onsite-upload=true`}`}
                    renderAs="canvas"
                />
            </div>
            <div className={`onsite_text_ sGFfonte-${UgcSettings.onsite_txt_font_variation}`}
                style={{
                fontSize: UgcSettings.onsite_txt_font_size,
                fontFamily: UgcSettings.onsite_txt_font,
                paddingRight: '5px',
                paddingLeft: '5px',
                }}
            >
                {(UgcSettings.onsite_qr_code_btn_text.length && String(UgcSettings.onsite_qr_code_btn_text).length > 0) ? UgcSettings.onsite_qr_code_btn_text : "SCAN TO SHARE"}
            </div>
        </div>
        </div>
    </>
    );
}
}
export default memo(OnSiteBanner);
